import { Enum } from "../Const/Enum";

/**
 * Switch View Data
 */

 export default class SwitchViewData{
     readonly switchType : number;
     readonly prefabPath : string;

     constructor(prefabPath : string, switchType? : number){
        this.prefabPath = prefabPath;
        this.switchType = switchType || Enum.SWITCH_TYPE.View;
     }
 }